@import "../../variables.scss";

$border-radius: 5%;

.PortfolioItem {
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    border: 4px solid $accent-color;
    border-radius: $border-radius;
  }

  .details {
    color: $accent-color;
    margin-top: 10px;
    display: flex;
    align-items: center;
    text-decoration: none;

    .icon {
      font-size: 20px;
    }

    .techStack {
      margin-left: 5px;
    }
  }
}
